body {
  margin: 0 auto;
  font-family: "Noto Sans Math", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background-color: #fafafa;
  background: linear-gradient(to bottom, rgb(8, 8, 8) 0%, rgb(8, 8, 8) 40%, rgb(56, 66, 92) 70%, rgb(56, 66, 92) 100%);
  background-position: right;
  background-size: cover;
}

p, ul, ol {
  color: #000000;
  font-size: 1.7rem;
}
@media (min-width: 1024px) {
  p, ul, ol {
    font-size: 2rem;
  }
}

.contenedor {
  width: 95%;
  max-width: 140rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
  color: rgb(101, 121, 126);
  font-weight: 700;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Eboracum", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

main {
  margin: 0 auto;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.list-bullets {
  list-style-type: circle;
  padding-left: 8rem;
  text-align: left;
}
.list-bullets.gradcap {
  list-style-type: none;
  list-style-image: url(../../../build/img/gradcap_small.svg);
}
.list-bullets.gradcap li {
  margin-left: 0;
}

.read-more-button {
  height: 3rem;
  width: 20%;
  transition: all 0.25s cubic-bezier(0.75, 0, 0.47, 1);
  background: #6b6969;
  margin: 0 0 1rem 5rem;
  border-radius: 1rem;
  text-align: center;
  justify-content: center;
}
.read-more-button:hover {
  cursor: pointer;
  transform: scale(1.1);
  background: #333333;
}
.read-more-button a {
  color: white;
}

.list-nobullets {
  list-style-type: none;
  padding-left: 0;
}

.fixed-top {
  position: fixed;
}

.single-container {
  background: #fafafa;
  box-shadow: 2px -1px 16px -10px rgba(0, 0, 0, 0.75);
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 80%;
  max-width: 140rem;
  margin-top: 8rem;
  margin-bottom: 2rem;
}
.single-container h1 {
  margin-top: 2rem;
}
.single-container p {
  margin: 0 5rem 5rem 5rem;
}
.single-container .social-share {
  margin-bottom: 5rem;
  display: flex;
  flex-direction: row;
  align-items: center;
}
.single-container .social-share .share-twitter,
.single-container .social-share .share-linkedin {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-left: 1rem;
  margin-right: 1rem;
}
.single-container h1, .single-container h2, .single-container h3, .single-container h4 {
  margin: 2rem 5rem 1rem 5rem;
}

.smaller-font p {
  font-size: 1.7rem;
}
.smaller-font ul li {
  font-size: 1.7rem;
}
@media (min-width: 768px) {
  .smaller-font ul li {
    font-size: 1.7rem;
  }
}

.barra {
  display: flex;
  flex-direction: column;
  text-align: center;
  width: 100%;
  align-items: center;
  background-color: #000000;
  transition: 0.5s;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    justify-content: center;
  }
}
.barra .brand {
  font-family: "Eboracum", sans-serif;
  font-size: 2rem;
  margin: 0 2rem 0 4rem;
  color: #fafafa;
}
.barra .navegacion {
  flex: 1;
  display: flex;
  height: 100%;
  text-align: center;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  visibility: hidden;
  opacity: 0;
  max-height: 0;
  overflow: hidden;
  transition: visibility 0.15s cubic-bezier(0.17, 0.67, 0.83, 0.67) 0.15s, opacity 0.5s cubic-bezier(0.17, 0.67, 0.83, 0.67) 0.2s;
}
@media (min-width: 768px) {
  .barra .navegacion {
    flex-direction: row;
    display: flex;
    opacity: 1;
    max-height: 200rem;
    visibility: visible;
  }
}
.barra .navegacion.mostrar {
  display: flex;
  opacity: 1;
  visibility: visible;
  max-height: 200rem;
}
.barra .navegacion a {
  display: flex;
  font-size: 2rem;
  padding: 0 1.5rem 0 1.5rem;
  color: #fafafa;
  text-align: center;
  align-items: center;
  height: 100%;
  cursor: pointer;
}
@media (min-width: 768px) {
  .barra .navegacion a {
    padding-bottom: 1rem;
    padding-top: 1rem;
  }
}
.barra .navegacion a:hover {
  color: #000000;
  background-color: #fafafa;
  font-weight: 900;
}
.barra .language-dark {
  display: flex;
  margin: 0 4rem 0 2rem;
  align-items: center;
}
.barra .language-dark .dark-mode-button img {
  filter: invert(1);
  width: 4rem;
  margin-right: 1rem;
}
.barra .language-dark .dark-mode-button:hover {
  transform: scale(1.1);
  cursor: pointer;
}
.barra .language-dark svg {
  margin-right: 1rem;
  width: 3rem;
  height: 3rem;
}
.barra .language-dark a {
  font-size: 1.8rem;
  color: #fafafa;
}
.barra .language-dark .disabled-a {
  pointer-events: none;
  cursor: default;
  color: gray;
}

.menu-hamburguesa {
  width: 5rem;
  height: 5rem;
  display: block;
}
@media (min-width: 768px) {
  .menu-hamburguesa {
    display: none;
  }
}
.menu-hamburguesa:hover {
  transform: scale(1.1);
}
.menu-hamburguesa div {
  background: #fafafa;
  height: 0.5rem;
  width: 100%;
  margin-bottom: 1rem;
  border-radius: 0.5rem;
}
.menu-hamburguesa div:first-of-type {
  margin-top: 1rem;
  transition: 0.5s;
}
.menu-hamburguesa div:last-of-type {
  margin-bottom: 0;
  transition: 0.5s;
}
.menu-hamburguesa.is-opened .top-burger {
  transform: rotate(45deg);
  margin-bottom: 0;
  margin-top: 2rem;
}
.menu-hamburguesa.is-opened .bottom-burger {
  transform: translateY(-2rem) rotate(-45deg);
  margin-bottom: 0;
  margin-top: 0;
}
.menu-hamburguesa.is-opened .middle-burger {
  visibility: hidden;
}

.no-webp .inicio {
  background-image: url(../img/StuckPackoff1.jpg);
}

.webp .inicio {
  background-image: url(../img/StuckPackoff1.webp);
}

.header {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
}
.header .contenido-header {
  display: flex;
  width: 100%;
  background-color: #33302B;
  min-height: 3rem;
  position: fixed;
  top: 0;
  z-index: 999;
}
.header.inicio {
  height: 100vh;
  background-position: bottom center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 60rem;
  }
}
.header .texto-inicio {
  text-align: center;
  color: #fafafa;
  padding-bottom: 5rem;
}
.header .texto-inicio h3 {
  opacity: 0;
  animation-name: popup;
  animation-duration: 0.5s;
  animation-iteration-count: 1;
  animation-delay: 0;
  animation-fill-mode: forwards;
}
.header .texto-inicio p {
  color: #fafafa;
  font-size: 2rem;
  animation-name: popup;
  animation-duration: 0.5s;
  animation-iteration-count: 1;
  animation-delay: 0.25s;
  animation-fill-mode: forwards;
  opacity: 0;
}
.header .texto-inicio .iconos {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: start;
  animation-name: popup;
  animation-duration: 0.5s;
  animation-iteration-count: 1;
  animation-delay: 0.5s;
  animation-fill-mode: forwards;
  opacity: 0;
}
.header .texto-inicio .iconos img {
  width: 3rem;
  height: 3rem;
  margin: 0 1.5rem 0 1.5rem;
}
.header .texto-inicio .iconos img:hover {
  transform: scale(1.1);
  cursor: pointer;
}
.header .texto-inicio .contenedor-flecha {
  padding-top: 3rem;
  position: relative;
  z-index: 0;
}
.header .texto-inicio .contenedor-flecha img {
  margin-top: 1rem;
  position: absolute;
  animation: 1s infinite alternate slidein;
}

@keyframes slidein {
  from {
    top: 0;
  }
  to {
    top: 1rem;
  }
}
@keyframes popup {
  0% {
    transform: scale(0);
  }
  75% {
    transform: scale(1.1);
    opacity: 1;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}
footer {
  background-color: #000000;
  margin-bottom: 0;
  margin-top: 3rem;
}
footer .contenedor-footer {
  padding-top: 2.5rem;
  display: flex;
  height: 12rem;
  flex-direction: column;
  position: relative;
  text-align: center;
}
footer .contenedor-footer .iconos {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: start;
}
footer .contenedor-footer .iconos img {
  width: 3rem;
  height: 3rem;
  margin: 0 1.5rem 0 1.5rem;
}
footer .contenedor-footer .iconos img:hover {
  transform: scale(1.1);
  cursor: pointer;
}
footer .contenedor-footer .copyright {
  position: absolute;
  width: 100%;
  color: #fafafa;
  font-size: 1.5rem;
  bottom: 1rem;
}
footer .imagen-rig {
  max-height: 100rem;
  min-height: 50rem;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: left;
  overflow: hidden;
  position: relative;
  background: linear-gradient(to top, rgba(8, 8, 8, 0) 0%, rgba(8, 8, 8, 0) 40%, rgba(56, 66, 92, 0.6) 70%, rgb(56, 66, 92) 90%, rgb(56, 66, 92) 100%), url(/Imagenes/rig5.jpg);
  background-position: bottom;
  background-size: cover;
  background-blend-mode: normal;
}
@media (min-width: 768px) {
  footer .imagen-rig {
    min-height: 90rem;
  }
}
footer .imagen-rig img {
  width: 100%;
  height: auto;
  position: absolute;
  z-index: 1;
  filter: opacity(70%);
  object-fit: cover;
}
footer .imagen-rig h1 {
  font-size: 10rem;
  bottom: 0%;
  position: absolute;
  top: 0;
  z-index: 5;
  margin-left: 2rem;
}

.about-me {
  width: 90%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .about-me {
    width: 70%;
  }
}

.info-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 2rem;
  z-index: 1;
}
@media (min-width: 1024px) {
  .info-container {
    display: grid;
    grid-template-columns: 1.3fr 3fr;
    align-items: start;
    gap: 1rem;
  }
}
.info-container .foto-and-info {
  background-color: #fafafa;
  width: 100%;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  padding-top: 2rem;
  box-shadow: 2px -1px 16px -10px rgba(0, 0, 0, 0.75);
  border-radius: 1rem;
  margin-bottom: 4rem;
  margin-right: 0;
  align-items: center;
}
@media (min-width: 1024px) {
  .info-container .foto-and-info {
    width: 100%;
    padding-left: 1rem;
    padding-right: 1rem;
    margin-right: 2rem;
    position: sticky;
    top: 6rem;
  }
}
.info-container .foto-and-info img {
  padding: 0;
  width: 70%;
  height: 100%;
}
@media (min-width: 1024px) {
  .info-container .foto-and-info img {
    width: 90%;
    padding-bottom: 0;
    padding: 0 2rem 0 2rem;
  }
}
.info-container .foto-and-info .linkedin-post {
  display: none;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 1024px) {
  .info-container .foto-and-info .linkedin-post {
    display: flex;
    margin: 1rem 0 1.5rem 0;
  }
}
.info-container .foto-and-info .linkedin-post .title-pinned-post {
  display: flex;
  flex-direction: row;
}
.info-container .foto-and-info .linkedin-post .title-pinned-post p {
  margin: 0 0 0 0;
  font-size: 1.8rem;
  font-weight: 700;
}
.info-container .foto-and-info .linkedin-post iframe {
  width: 100%;
  max-height: 90rem;
  padding: 0 2rem 0 2rem;
}
@media (min-width: 1024px) {
  .info-container .foto-and-info .linkedin-post iframe {
    max-height: 35rem;
  }
}
@media (min-width: 1440px) {
  .info-container .foto-and-info .linkedin-post iframe {
    max-height: 30rem;
  }
}
.info-container .foto-and-info .texto-inicio {
  text-align: center;
  color: #000000;
  padding-bottom: 0;
}
.info-container .foto-and-info .texto-inicio p {
  color: #000000;
  font-size: 2rem;
  margin: 1rem 0 0 0;
}
.info-container .foto-and-info .texto-inicio .iconos {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: start;
  margin-bottom: 2rem;
}
.info-container .foto-and-info .texto-inicio .iconos img {
  height: 3rem;
  width: auto;
  margin: 0 0.5rem 0 0.5rem;
}
.info-container .foto-and-info .texto-inicio .iconos img:hover {
  transform: scale(1.1);
  cursor: pointer;
}
.info-container .foto-and-info .about-text {
  display: flex;
  flex-direction: column;
  width: 60%;
}
.info-container .right-info-container {
  display: flex;
  flex-direction: column;
  box-shadow: 2px -1px 16px -10px rgba(0, 0, 0, 0.75);
  align-items: center;
  width: 100%;
}
.info-container .right-info-container .about-text ul {
  padding-left: 8rem;
}
.info-container .right-info-container .about-text ul li {
  margin-left: 0;
  list-style: circle;
  font-size: 1.7rem;
}
.info-container .right-info-container .about-info {
  background-color: #fafafa;
  width: 100%;
  min-height: 20rem;
  justify-content: center;
  padding-left: 0;
  position: relative;
  padding-bottom: 5rem;
  border-radius: 1rem;
  margin-bottom: 4rem;
}
@media (min-width: 1024px) {
  .info-container .right-info-container .about-info {
    padding-left: 2rem;
    padding-right: 2rem;
    width: 100%;
  }
}
.info-container .right-info-container .about-info h1 {
  margin-top: 0;
  padding-top: 2rem;
}
.info-container .right-info-container .about-info p {
  margin: 1rem 2rem 0 2rem;
  padding-bottom: 2rem;
}
@media (min-width: 1024px) {
  .info-container .right-info-container .about-info p {
    margin: 1rem 5rem 0 5rem;
  }
}
.info-container .right-info-container .about-info .interests-education {
  display: flex;
  flex-direction: column;
}
@media (min-width: 1024px) {
  .info-container .right-info-container .about-info .interests-education {
    display: grid;
    grid-template-columns: 2fr 2fr;
    gap: 0;
    padding-top: 2rem;
  }
}
.info-container .right-info-container .about-info .interests-education h2 {
  margin: 0 2rem 0 2rem;
}
.info-container .right-info-container .about-info .interests-education ul {
